}

.o-grid-container {
  position: relative;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  min-width: 240px;
  max-width: 1220px;
  max-width: 1220px;
  padding-left: 10px;
  padding-right: 10px;
}

@media (min-width: 46.25em) {
  .o-grid-container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (min-width: 46.25em) {
  .o-grid-snappy .o-grid-container, .o-grid-container--snappy {
    max-width: 740px;
  }
}

@media (min-width: 61.25em) {
  .o-grid-snappy .o-grid-container, .o-grid-container--snappy {
    max-width: 980px;
  }
}

@media (min-width: 76.25em) {
  .o-grid-snappy .o-grid-container, .o-grid-container--snappy {
    max-width: 1220px;
  }
}

@media \0screen {
  .o-grid-container {
    padding-left: 20px;
    padding-right: 20px;
    width: 980px;
  }
}

.o-grid-container--bleed {
  padding-left: 0;
  padding-right: 0;
}

.o-grid-row {
  clear: both;
  flex-wrap: wrap;
  display: flex;
  margin-left: -10px;
  zoom: 1;
}

@media print {
  .o-grid-row {
    display: inherit;
  }
}

@media (min-width: 46.25em) {
  .o-grid-row {
    margin-left: -20px;
  }
}

@media \0screen {
  .o-grid-row {
    margin-left: -20px;
  }
}

.o-grid-row:before, .o-grid-row:after {
  content: '';
  display: table;
  display: flex;
}

.o-grid-row:after {
  clear: both;
}

