.n-storylines__article {
  position: relative;
  margin-bottom: 20px;
}

.n-storylines__article::before {
  counter-increment: list;
  content: counter(list);
  position: absolute;
  font: 14px/15px MetricWeb, sans-serif;
  color: #fff1e0;
  left: -35px;
  top: 2px;
  text-align: center;
  background-color: #1d1d1d;
  border-radius: 100%;
  height: 15px;
  width: 15px;
  padding: 5px;
}

.n-storylines__top-content {
  counter-reset: list;
  width: 85%;
  position: relative;
}

.n-storylines__top-content::before {
  content: '';
  position: absolute;
  border-left: 1px solid #cec6b9;
  top: 5px;
  bottom: 35px;
  left: 17px;
  margin: 0;
}

p {
  margin: 0;
}

.n-storyline-timestamp {
  font-family: MetricWeb, sans-serif;
  line-height: 16px;
  font-size: 14px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.6);
}

.n-storylines__article-link {
  font-family: FinancierDisplayWeb, serif;
  line-height: 24px;
  font-size: 20px;
  font-weight: 400;
  color: #505050;
  text-decoration: none;
}

.n-storylines__article-link:focus {
  cursor: pointer;
  color: #777777;
}

.n-storylines__article-header {
  font-family: MetricWeb, sans-serif;
  line-height: 16px;
  font-size: 14px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.6);
}

.n-storylines__timeline-container {
  display: flex;
  position: relative;
  width: 100%;
  margin-bottom: 20px;
  background: #ffe9d7;
}

.n-storylines__timeline-container:before {
  border-top: 1px solid #cec6b9;
  content: "";
  margin: 0 auto;
  position: absolute;
  top: 35px;
  left: -30%;
  width: 150%;
  z-index: -1;
}

.parent-level {
  padding: 26px 8px 0 0;
  font-family: MetricWeb, sans-serif;
  line-height: 17px;
  font-size: 15px;
  font-weight: 600;
  color: #26747a;
}

.n-storylines__back-btn {
  margin: 17px -5px 0 -5px;
  background-image: url("https://www.ft.com/__origami/service/image/v2/images/raw/fticon-v1:arrow-left?source=o-icons&tint=%2326747A,%2326747A&format=svg");
  background-image: url("https://www.ft.com/__origami/service/image/v2/images/raw/fticon-v1:arrow-left?source=o-icons&tint=%2326747A,%2326747A&format=png&width=36") \9;
  width: 36px;
  height: 36px;
  display: inline-block;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50%;
  background-color: transparent;
  vertical-align: baseline;
}

.n-storylines__scroll {
  overflow-x: auto;
  white-space: nowrap;
  display: flex;
  position: relative;
  background: #ffe9d7;
  width: 100%;
}

.n-storylines__heatmap-segment {
  width: 100%;
  margin-top: 10px;
  text-align: center;
}

.n-storylines__heatmap-segment-colour,
.n-storylines__heatmap-segment-none {
  background: #9e2f50;
  width: 100%;
  height: 50px;
  display: flex;
}

.n-storylines__heatmap-segment-name {
  font-family: MetricWeb, sans-serif;
  line-height: 17px;
  font-size: 15px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.6);
  position: relative;
  margin-top: 10px;
}

.n-storylines__heatmap-segment-name::before {
  counter-increment: dot;
  content: '';
  position: absolute;
  top: -95%;
  left: 45%;
  background-color: #ffe9d7;
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 100%;
  height: 9px;
  width: 9px;
}

.no-articles {
  opacity: 0.6;
}

.n-storylines__all-months {
  min-width: 50px;
}

.dot-container {
  list-style-type: none;
  padding: 0;
  margin: 0;
  position: relative;
  margin-right: 20px;
}

.dot {
  font-family: MetricWeb, sans-serif;
  line-height: 13px;
  font-size: 11px;
  font-weight: 400;
  position: absolute;
  background: #1d1d1d;
  top: -10px;
  height: 10px;
  width: 10px;
  border-radius: 100%;
  text-align: center;
  font-size: 10px;
  line-height: 10px;
  color: #ffe9d7;
  padding: 5px;
  z-index: 3;
  margin-right: -4%;
}

